.works {
  position: relative;
}

.works .overview {
  width: 900px;
  border-bottom: 1px solid #3d3d3d;
  padding: 45px 0px;
  display: flex;
  align-items: center;
  gap: 35px;
}

.works .overview-text {
  display: flex;
  flex-direction: column;
  gap: 16px;
  font-size: 15px;
}

.works .url {
  text-decoration: underline;
  cursor: pointer;
  transition-duration: 0.4s;
}

.works .url:hover {
  color: #e90101;
}

.titleAlt {
  height: 40px;
  width: 900px;
  border-bottom: 2px solid #d1d1d1;
  font-size: 20px;
  font-weight: bold;
  color: #d1d1d1;
  display: flex;
  justify-content: space-between;
}

.titleAlt-detail {
  font-size: 15px;
  font-weight: normal;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition-duration: 0.4s;
}

.titleAlt-detail:hover {
  color: white;
}

.works .blur {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;

  background-color: black;
  opacity: 0.7;
  z-index: 900;

  display: none;
}

.works .blur.visible {
  display: block;
}

.works .detail {
  position: fixed;
  z-index: 1000;
  top: 50px;
  left: 260px;
  width: 1340px;
  height: 820px;
  padding-top: 50px;
  padding-bottom: 100px;
  border: 1px solid #3d3d3d;
  background-color: black;
  overflow-y: auto;

  opacity: 0;
  transition: opacity 0.4s;
  pointer-events: none;
}

.works .detail.visible {
  opacity: 1;
  pointer-events: auto;
}

.works .detail-content-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 900px;
}

.works .big-title {
  font-size: 40px;
  font-weight: bold;
}

.works .detail-content {
  width: 960px;
  padding: 45px 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  border-bottom: 1px solid #3d3d3d;
}

.works .detail-content p {
  line-height: 1.5em;
}

.works .overview-box {
  width: 960px;
  padding: 45px 20px;
  display: flex;
  flex-direction: column;
  gap: 30px;
  border-bottom: 1px solid #3d3d3d;
}

.works .overview-line {
  color: #d1d1d1;
  font-size: 16px;
  display: flex;
  align-items: center;
  gap: 13px;
}

.works .overview-line-tag {
  color: #1d1d1d;
  background-color: #d1d1d1;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px 13px;
  cursor: default;
  transition-duration: 0.4s;
}

.works .overview-line-tag:hover {
  background-color: white;
}
